// COLOR PALETTE

// LAYOUT
$layoutPadding: 20px;

// THEME COLORS
$main: #e91e63e3;
$secondary: #2c3e50;

// LAYOUT BREAKPOINTS
$screen-m: 800px;
$screen-l: 1000px;
$screen-xl: 1420px;

// Endpoints
$white: #fff;
$black: #000;

// Greys
$light-grey: #f5f5f5;
$mid-grey: #d7dada;
$dark-grey: #353b3b;

// Default Color
$default: $dark-grey;

$turquoise: #1abc9c;
$green-sea: #16a085;
$emerald: #2ecc71;
$nephritis: #27ae60;
$peter-river: #3498db;
$belize-hole: #2980b9;
$amethyst: #9b59b6;
$wisteria: #8e44ad;
$wet-asphalt: #34495e;
$midnight-blue: #2c3e50;
$sunflower: #f1c40f;
$orange: #f39c12;
$carrot: #e67e22;
$pumpkin: #d35400;
$alizarin: #e74c3c;
$pomegranate: #c0392b;
$clouds: #ecf0f1;
$silver: #bdc3c7;
$concrete: #95a5a6;
$asbestos: #7f8c8d;
$bgc1: #eaf1fb;
$bgc2: #f7f8fc;
$bgc3: #d3e3fd;

// Message Colors
$msg-default: #999;
$msg-success: #5cb85c;
$msg-warning: #f0ad4e;
$msg-danger: #d9534f;
$msg-info: #5bc0de;

//Social Colors
$dailymile: #ffaa25;
$facebook: #3b5998;
$flickr: #f80082;
$github: #f7f7f7;
$google: #d7473d;
$instagram: #37658d;
$linkedin: #2c8cc0;
$pinterest: #c5142b;
$runkeeper: #3193d3;
$skype: #00abee;
$spotify: #7ab800;
$twitter: #3b8aca;

// Set Site Specific Base, Highlight and Accent Colors Here for easy switching between themes
$base: $dark-grey;
$highlight: $belize-hole;
$accent: $peter-river;
