.robot-preview {
  display: flex;
  flex-direction: column;
  background-color: #e91e630d;
  text-align: center;
  box-shadow: -2px 1px 11px #00000026;
  border-radius: 5px;
  background-size: min(70%, 40vw);
  background-repeat: no-repeat;
  background-position: bottom right;
  cursor: pointer;
  overflow-y: hidden;
  .info {
    padding: 10px;
    flex-grow: 1;
  }

  .actions {
    height: 25%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #000000cf;
    padding: 10px;
    transform: translateY(100%);
    transition: transform 0.2s;

    button,
    a {
      background: none;
      color: whitesmoke;
      border: none;
      // font-weight: 600;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &:hover .actions {
    transform: translateY(0);
  }
}
